import { Badge, Col, Row } from "antd";
import { useCallback, useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { TaskSprintManageService } from "../../../../services/taskService";
import { Box, MyBox } from "../../../core/MyBox";
import { MyButton } from "../../../core/MyButton";
import { UIFormatDateVNComponent } from "../../../core/UI_FormatComponent";
import ModifyComponent from "./DetailComponent";
import "../task.css";
import TableComponent from "../../../core/TableNewComponent";
import { OrderByModel, PageInfoModel } from "../../../core/TableModel";
import { PaginationRequest, TableColumn } from "../../../../models/baseModel";
import { Fn } from "../../../../helper/function";

export default function IndexComponent() {
    const [isLoading, setIsLoading] = useState(false);
    const [data, setData] = useState([]);
    const [visible, setVisible] = useState(false);
    const [visibleEdit, setVisibleEdit] = useState(false);
    const [keyEdit, setKeyEdit] = useState<string | undefined>(undefined);

    const [projectName, setProjectName] = useState();
    const [pageInfo, setPageInfo] = useState<PageInfoModel>(new PageInfoModel());
    const [orderBy, setOrderBy] = useState<OrderByModel>(new OrderByModel());
    const column: any = [
        new TableColumn({
            title: "STT", key: "stt", render: (data: any, row: any, index: number) =>
                Fn.RenderSTT(data, row, index, pageInfo)
        }),
        new TableColumn({
            title: "Nội dung", key: "name", render: (data: string, row: any) => {
                return (
                    <MyButton type="link" value={data} onClick={() => {
                        handleClickEditSprint(row.key)
                    }} />
                )
            }
        }),
        new TableColumn({
            title: "Trạng thái", key: "status", render: (data: any, row: any) => {
                let status: any;
                let text: string = row.status?.value;

                if (row.status?.isNew) {
                    status = "warning";
                } else if (row.status?.isClosed) {
                    status = "error";
                } else {
                    status = "processing";
                }
                return <Badge status={status} text={text} />
            }
        }),
        new TableColumn({
            title: "Ngày bắt đầu", key: "dateFrom", render: (data: string) => {
                return (
                    <UIFormatDateVNComponent value={data} />
                );
            }
        }),
        new TableColumn({
            title: "Ngày kết thúc", key: "dateTo", render: (data: string) => {
                return (
                    <UIFormatDateVNComponent value={data} />
                );
            }
        }),
    ];

    const keyData: string | undefined = useParams().key;

    const loadData = useCallback(() => {
        setIsLoading(true);
        let paginationRequest = new PaginationRequest(pageInfo.pageNumber, pageInfo.pageSize, orderBy);
        TaskSprintManageService.GetList(keyData, paginationRequest)
            .then((rs: any) => {
                if (rs.isSuccess) {
                    setPageInfo(new PageInfoModel(rs));
                    setData(rs.data.pagedData);
                    if(rs.data.pagedData.length !== 0) {
                        setProjectName(rs.data.pagedData[0].project.name);
                    }
                }
            })
            .finally(() => setIsLoading(false))
    }, [setIsLoading, keyData, pageInfo.pageNumber, pageInfo.pageSize, orderBy]);

    useEffect(() => {
        loadData()
    }, [loadData]);

    function handleClickAddSprint() {
        setVisible(true);
    }

    function handleClickEditSprint(key: string | undefined) {
        setVisibleEdit(true);
        setKeyEdit(key);
    }

    return (
        <MyBox
            title="Quản lý giai đoạn"
            isLoading={isLoading}
            buttonList={() => (
                <MyButton
                    type="primary"
                    value="Thêm mới"
                    onClick={handleClickAddSprint} />
            )}>
            <Row gutter={12}>
                <Col span={24}>
                    <h3><strong>Tên dự án: </strong>{projectName}</h3>
                </Col>
                <Col span={6}>
                    <Box>
                        Công việc mới chưa xử lý
                    </Box>
                </Col>

                <Col span={6}>
                    <Box>
                        Công việc cần làm
                    </Box>
                </Col>

                <Col span={6}>
                    <Box>
                        Công việc sắp hết hạn
                    </Box>
                </Col>

                <Col span={6}>
                    <Box>
                        Vấn đề chưa được giải quyết
                    </Box>
                </Col>


                {/* 
                <Col span={4}>
                    <Row gutter={12}>
                        {RenderItem()}
                    </Row>
                </Col>
                <Col span={20}>
                    <Box>
                        <h3>Danh sách task</h3>
                        <Link to="">Chi tiết</Link>
                    </Box>

                    <Box>
                        <h3>Danh sách issue</h3>
                        <Link to="">Chi tiết</Link>
                    </Box>

                    <Box>
                        <h3>To do list</h3>
                        <Link to={`/tools/tasks/projects/${keyData}/sprint/${sprintChoose}/toDoList`}>
                            <MyButton type="primary" disabled={sprintChoose === undefined} value="Chi tiết" />
                        </Link>
                    </Box>

                    <Box>
                        <h3>Tài liệu dự án</h3>
                        <Link to="">Chi tiết</Link>
                    </Box>
                </Col> */}
            </Row>

            <TableComponent
                pageInfo={pageInfo}
                setPageInfo={setPageInfo}
                orderBy={orderBy}
                setOrderBy={setOrderBy}
                loadData={loadData}
                column={column}
                dataSource={data}
                loading={isLoading} />

            <ModifyComponent
                projectKey={keyData}
                visible={visible}
                setVisible={setVisible}
                isLoading={isLoading}
                setIsLoading={setIsLoading}
                loadData={loadData} />

            <ModifyComponent
                keyData={keyEdit}
                setKeyData={setKeyEdit}
                projectKey={keyData}
                visible={visibleEdit}
                setVisible={setVisibleEdit}
                isLoading={isLoading}
                setIsLoading={setIsLoading}
                loadData={loadData} />
        </MyBox>
    );
}